<template>
  <div>
    <div class="input-wrapper" :id="elementId"></div>
    <a class="btn" href="#" @click.prevent="proceedPayment">Pay</a>
  </div>
</template>

<script>
const stripe = Stripe(process.env.VUE_APP_STRIPE_PUBLISHABLE_KEY)
const elements = stripe.elements()

export default {
  name: 'CardForm',
  data () {
    return {
      elementId: 'card-element',
      completeCard: false,
      displayError: false,
      errorValue: '',
      elementRef: null
    }
  },
  mounted () {
    let el = elements.create('card')
    
    el.mount(`#${ this.elementId }`)

    //Keep ref to destroy element when destroying component
    this.elementRef = el

    el.on('change', (data) => {
      let proceed = false
      if (typeof data.error !== 'undefined') {
        this.displayError = true
        this.errorValue = data.error.message
      } else {
        this.displayError = false
        this.errorValue = ''
        if (data.complete) {
          proceed = true
        }
        this.completeCard = proceed
      }
    })
  },
  beforeDestroy () {
    this.elementRef.destroy()
  },
  methods:{
    proceedPayment () {
      let ownerInfo = {
        owner: {
          name: 'Mister Dudemeister',
          email: 'fake.email@example.com'
        }
      }

      //  For testing purpose. Remove when not needed anymore
      //  4000 0000 0000 3063 // Required 3D
      //  4000 0000 0000 3055 // Optional 3D
      //  3782 8224 6310 005  // Not supported

      stripe.createSource(this.elementRef, ownerInfo)
        .then((result) => {
          if (result.error) {
            
            console.log(err)
          } else {
            // Send the source to your server
            
            console.log(result.source)
            
            if (result.source.card.three_d_secure !== 'not_supported') {
              
              stripe.createSource({
                type: 'three_d_secure',
                amount: 1099,
                currency: "eur",
                three_d_secure: {
                  card: result.source.id
                },
                redirect: {
                  return_url: "http://localhost:8080/process-payment"
                }
              }).then((result) => {
                console.log(result.source)
                if (result.error) {
                  this.$toasted.error('Error creating 3D source!');
                } else {
                  if (result.source.status !== 'chargeable') {
                    location.href = result.source.redirect.url
                  } else {
                    alert('Send source to API for server charging')
                  }
                }
              })

            } else {
              alert('Send source to API for server charging')
            }
          }
        })
        .catch(err => {
          alert('Error getting token!');
          console.log(err)
        })
    }
  }
}
</script>

