      :background-color="backgroundColor"
      :border-color="borderColor"
      :white-label="whiteLabel"
    />
  </div>
</template>

<script>
  export default {
    name: 'SectionTwo',
    data(){
      return {
        content: '',
        backgroundColor: '',
        borderColor: '',
        whiteLabel: true
      }
    },
